<template>
  <div>
    <el-container>
      <!-- 顶栏标题 -->
      <el-header style="font-size: 40px;background-color: rgb(177, 199, 236)">学生宿舍管理系统</el-header>
      <el-container>
        <!-- 侧边菜单 -->
        <el-aside width="200px" style="background-color: rgb(238, 241, 246)">
          <el-menu :default-openeds="['1', '3']">
            <el-menu-item index="1-1"><router-link to="/stu">学生个人信息</router-link></el-menu-item>
            <el-menu-item index="1-2"><router-link to="/room">宿舍规格信息</router-link></el-menu-item>
          </el-menu>
        </el-aside>

        <!-- 主体 -->
        <el-main>
          <!-- 第一行功能按钮 -->
          <div>
            <!-- 添加宿舍信息按钮,dialog对话框form表单 -->
            <el-button type="primary" round @click="dialogFormVisible = true">添加宿舍信息</el-button>
            <el-dialog title="填写信息" :visible.sync="dialogFormVisible">
              <el-form :model="form" ref="form">
                <el-form-item label="楼栋" :label-width="formLabelWidth" prop="unit" :rules="[
                  { required: true, message: '不能为空' }]">
                  <el-input v-model="form.unit" autocomplete="off"></el-input>
                </el-form-item>
                <el-form-item label="宿舍号" :label-width="formLabelWidth" prop="roomid" :rules="[
                  { required: true, message: '不能为空' },
                  { type: 'number', message: '必须为数字' }]">
                  <el-input v-model.number="form.roomid" autocomplete="off"></el-input>
                </el-form-item>
                <el-form-item label="楼层" :label-width="formLabelWidth" prop="floor" :rules="[
                  { required: true, message: '不能为空' },
                  { type: 'number', message: '必须为数字' }]">
                  <el-input v-model.number="form.floor" autocomplete="off"></el-input>
                </el-form-item>
                <el-form-item label="床位数" :label-width="formLabelWidth" prop="bedcount" :rules="[
                  { required: true, message: '不能为空' },
                  { type: 'number', message: '必须为数字' }]">
                  <el-input v-model.number="form.bedcount" autocomplete="off"></el-input>
                </el-form-item>
                <el-form-item label="住宿费" :label-width="formLabelWidth" prop="money" :rules="[
                  { required: true, message: '不能为空' },
                  { type: 'number', message: '必须为数字' }]">
                  <el-input v-model.number="form.money" autocomplete="off"></el-input>
                </el-form-item>
              </el-form>

              <div slot="footer" class="dialog-footer">
                <el-button round @click="resetForm('form')">重置</el-button>
                <el-button round @click="dialogFormVisible = false">取消</el-button>
                <el-button type="primary" round @click="form_Submit">确定</el-button>
              </div>
            </el-dialog>
          </div>

          <br>

          <!-- 条件查询 -->
          <el-form :inline="true" :model="searchForm" class="demo-form-inline" ref="searchForm">
            <el-form-item label="楼栋" prop="unit">
              <el-input v-model="searchForm.unit" placeholder="请输入楼栋"></el-input>
            </el-form-item>

            <el-form-item label="宿舍号" prop="roomid">
              <el-input v-model="searchForm.roomid" placeholder="请输入宿舍号"></el-input>
            </el-form-item>

            <el-form-item label="楼层" prop="floor">
              <el-input v-model="searchForm.floor" placeholder="请输入楼层"></el-input>
            </el-form-item>

            <el-form-item label="床位数" prop="bedcount">
              <el-input v-model="searchForm.bedcount" placeholder="请输入床位数"></el-input>
            </el-form-item>

            <el-form-item label="住宿费" prop="money">
              <el-input v-model="searchForm.money" placeholder="请输入住宿费"></el-input>
            </el-form-item>

            <el-form-item>
              <el-button type="primary" @click="search_Submit">根据筛选条件查询</el-button>
              <el-button @click="resetForm('searchForm'),search_Submit()">重置</el-button>
            </el-form-item>
          </el-form>


          <!-- 批量删除按钮 -->
          <el-button type="danger" @click="delete_choose()">批量删除已勾选信息</el-button>
          <el-button @click="cancel_choose()">取消</el-button>
          <br><br>

          <!-- 表格数据展示 -->
          <div>
            <el-table :data="roomData" border style="width: 100%" @selection-change="handleSelectionChange" ref="multiple_Table">
              <el-table-column type="selection" width="40"></el-table-column>
              <el-table-column prop="roommsg" label="楼栋-宿舍号" sortable width="130"></el-table-column>
              <el-table-column prop="unit" label="楼栋" sortable width="100"></el-table-column>
              <el-table-column prop="roomid" label="宿舍号" sortable width="100"></el-table-column>
              <el-table-column prop="floor" label="楼层" sortable width="80"></el-table-column>
              <el-table-column prop="bedcount" label="床位数" width="100"></el-table-column>
              <el-table-column prop="money" label="住宿费" sortable width="100"></el-table-column>


              <el-table-column label="操作">
                <template slot-scope="scope">
                  <el-button type="primary" size="mini" @click="
                    //handleEdit(scope.row),
                    EditFormVisible = true,
                    editform.unit = scope.row.unit
                    editform.roomid = scope.row.roomid
                    editform.floor = scope.row.floor
                    editform.bedcount = scope.row.bedcount
                    editform.money = scope.row.money
                    editform.roommsg = scope.row.roommsg
                    ">编辑</el-button>
                  <el-dialog title="填写信息" :visible.sync="EditFormVisible">
                    <el-form :model="editform" ref="editform">
                      <el-form-item label="楼栋" :label-width="formLabelWidth" prop="unit" :rules="[
                        { required: true, message: '不能为空' }]">
                        <el-input v-model="editform.unit" autocomplete="off" :disabled="true"></el-input>
                      </el-form-item>
                      <el-form-item label="宿舍号" :label-width="formLabelWidth" prop="roomid" :rules="[
                        { required: true, message: '不能为空' },
                        { type: 'number', message: '必须为数字' }]">
                        <el-input v-model.number="editform.roomid" autocomplete="off" :disabled="true"></el-input>
                      </el-form-item>
                      <el-form-item label="楼层" :label-width="formLabelWidth" prop="floor" :rules="[
                        { required: true, message: '不能为空' },
                        { type: 'number', message: '必须为数字' }]">
                        <el-input v-model.number="editform.floor" autocomplete="off" :disabled="true"></el-input>
                      </el-form-item>
                      <el-form-item label="床位数" :label-width="formLabelWidth" prop="bedcount" :rules="[
                        { required: true, message: '不能为空' },
                        { type: 'number', message: '必须为数字' }]">
                        <el-input v-model.number="editform.bedcount" autocomplete="off"></el-input>
                      </el-form-item>
                      <el-form-item label="住宿费" :label-width="formLabelWidth" prop="money" :rules="[
                        { required: true, message: '不能为空' },
                        { type: 'number', message: '必须为数字' }]">
                        <el-input v-model.number="editform.money" autocomplete="off"></el-input>
                      </el-form-item>
                    </el-form>

                    <div slot="footer" class="dialog-footer">
                      <el-button round @click="resetForm('editform')">重置</el-button>
                      <el-button round @click="EditFormVisible = false">取消</el-button>
                      <el-button type="primary" round @click="edit_Submit()">确定</el-button>
                    </div>
                  </el-dialog>
                  &ensp;
                  <el-button type="danger" size="mini" @click="delete_ids(scope.row.roommsg)">删除</el-button>
                </template>
              </el-table-column>


            </el-table>
          </div>

        </el-main>
      </el-container>
    </el-container>

    <!-- 分页 -->
    <!-- <div class="block">
      <el-pagination :page-size="10" :pager-count="6" layout="prev, pager, next" :total="100"></el-pagination>
    </div> -->
  </div>
</template>

<style>
.demo-table-expand {
  font-size: 0;
}

.demo-table-expand label {
  width: 90px;
  color: #99a9bf;
}

.demo-table-expand .el-form-item {
  margin-right: 0;
  margin-bottom: 0;
  width: 50%;
}
</style>

<script>
import axios from 'axios';

// /*封装get方法*/
// export function get(url, params = {}) {
//   return new Promise((resolve, reject) => {
//     axios.get(url, { params: params }).then(response => {
//       resolve(response.data);
//     })
//       .catch(err => {
//         reject(err);
//       });
//   });
// }

// /*封装post方法*/
// export function post(url, data = {}) {
//   return new Promise((resolve, reject) => {
//     axios.post(url, data).then(response => {
//       resolve(response.data);
//     })
//       .catch(err => {
//         reject(err);
//       });
//   });
// }


export default {
  data() {
    return {
      //stuData: [],
      roomData: [],
      chooseData: [],

      form: {
        unit: '',
        roomid: '',
        floor: '',
        bedcount: '',
        money: '',
        roommsg: ''
      },

      editform: {
        unit: '',
        roomid: '',
        floor: '',
        bedcount: '',
        money: '',
        roommsg: ''
      },

      searchForm: {
        unit: '',
        roomid: '',
        floor: '',
        bedcount: '',
        money: '',
        roommsg: ''
      },

      dialogFormVisible: false,
      EditFormVisible: false,
      formLabelWidth: '120px',
    }
  },


  methods: {
    /*新增数据表单提交*/
    form_Submit() {
      if (this.form.unit == "" || this.form.unit == "unll" ||
        this.form.roomid == "" || this.form.floor == "" ||
        this.form.bedcount == "" || this.form.money == "" ||
        this.form.roomid == null || this.form.floor == null ||
        this.form.bedcount == null || this.form.money == null) {
        this.$message.error('提交失败！信息不可留空！');
      }
      else {
        axios.post("http://localhost:8080/room", this.form).then((res) => {
          if (res.data.msg == "room repeat") {
            this.$message.error('提交失败！该宿舍已存在！');
          } else {
            this.$message({ message: '提交成功！请刷新网页查看最新数据', type: 'success' });
            console.log(res.data);
          }
        }).catch(error => {
          this.$message.error('提交失败！该宿舍已存在！');
          console.error(error);
        });
        //alert(JSON.stringify(this.form));
      }
    },

    /*搜索表单提交*/
    search_Submit() {
      //alert(JSON.stringify(this.searchForm))
      axios.post("http://localhost:8080/room/search", this.searchForm).then((res) => {
        console.log(res.data.data);
        this.roomData = res.data.data;
        this.$message({ message: '查询成功！共' + this.roomData.length + '条数据', type: 'success' });
      }).catch(error => {
        console.error(error);
      });
    },

    /*提交修改*/
    edit_Submit() {
      if (this.editform.floor == "" || this.editform.bedcount == "" || this.editform.money == "" ||
        this.editform.floor == null || this.editform.bedcount == null || this.editform.money == null) {
        this.$message.error('提交失败！信息不可留空！');
      }
      else {
        axios.post("http://localhost:8080/room/update", this.editform).then((res) => {
          this.$message({ message: '修改成功！请刷新网页查看最新数据', type: 'success' });
          console.log(res.data.data);
        }).catch(error => {
          this.$message.error('提交失败！请检查信息后重试！');
          console.error(error);
        });
      }
    },

    /*批量删除传入roommsgs数组的宿舍*/
    delete_ids(roommsgs) {
      //alert(roommsgs)
      this.$confirm('此操作将永久删除指定宿舍所有信息并清空该宿舍内学生的住宿信息 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        axios.delete("http://localhost:8080/room/ids/" + roommsgs).then((res) => {
          console.log(res.data);
        }).catch(error => {
          console.error(error);
        });

        this.$message({type: 'success',message: '删除成功!请刷新网页查看最新数据'});
      }).catch(() => {
        this.$message({type: 'info',message: '已取消删除'});
      });
      //alert(JSON.stringify(data));
    },

    /*重置表单*/
    resetForm(formName) {
      this.$refs[formName].resetFields();
    },

    /*批量删除勾选内容 */
    delete_choose() {
      var ids=new Array();
      for (var i = 0; i < this.chooseData.length; i++)
        ids[i]=this.chooseData[i].roommsg;
      this.delete_ids(ids);
    },

    /*取消勾选*/ 
    cancel_choose(rows) {//带参则反选传入参数的行
      if (rows) 
        rows.forEach(row => {this.$refs.multiple_Table.toggleRowSelection(row);});
      else 
        this.$refs.multiple_Table.clearSelection();
    },

    /*当选择项发生变化时触发该事件*/
    handleSelectionChange(choose_data) {
      this.chooseData = choose_data;
      //alert(JSON.stringify(choose_data));
    },
  },

  /*生命周期函数*/
  mounted() {
    axios.post("http://localhost:8080/room/search", this.searchForm).then((res) => {
      console.log(res.data.data);
      this.roomData = res.data.data;
    }).catch(error => {
      console.error(error);
    });
  }
};
</script>